import React from 'react';
import { Link } from 'gatsby';
import  Grid from  '@material-ui/core/Grid';

import './HeaderTop.css';

const HeaderTop = () => {
    return(
        <div className='header-top' >
            <Grid container>
                <Grid item className='social-icons' xs={12} sm={4} >
                    social
                </Grid>
                <Grid item className='mobile' xs={12} sm={4} >
                    mobile
                </Grid>
                <Grid item className='email' xs={12} sm={4} >
                    email
                </Grid>

            </Grid>
        </div>
    )
}

export default HeaderTop;
